.no-webp .imagen-contacto{
    background-image: url(../img/house.jpg);
}
.webp .imagen-contacto {
    background-image: url(../img/house.jpg);
}
.imagen-contacto{
    background-position: center center;
    background-size:cover;
    padding: $separacion*2 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2{
        font-family: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p{
        font-size:1.8rem;
        color:$blanco;
    }
}
