@include tablet{
    .contenedor-anuncios{
       @include grid(3, 2rem);
    }
}
.seccion{
    h2,h1{
        font-weight: $bold;
    }
}
.anuncio{
  
    background-color: lighten($fondoanuncios,5);
    border: 1px solid darken($gris, 10);
 
    .contenido-anuncio{
        padding: 2rem;
    }
    h3,p{
        font-weight: $bold;
        margin: 0;
    }
    .precio{
        color: $verde;
        font-weight:$bold;
        font-size: 2,5rem;
    }
  
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    li{
        flex:1;
        display: flex;
        img{
            flex: 0 0 5rem;
            margin-right:1rem ;
        }
        p{
            font-weight: black;
        }
    }

}


.seccion-anuncios{
    h1{
        font-weight:bold;
    }
}